.buttonBack {
  display: block;
  width: 100px;
  cursor: pointer;
  border: none;
  padding: 5px 0;
  background-color: white;
  color: black;
  border-radius: 5px;
  font-weight: 500;
  text-decoration: none;
  text-align: center;
}

.buttonBack:hover {
  background-color: pink;
  color: white;
  box-shadow: 10px 10px 10px -10px rgba(0, 0, 0, 0.41);
}
